
import { defineComponent, ref, onMounted} from 'vue'

import Navi from '../components/Navi.vue'
import Bg from "../components/Bg.vue"
import Product from "../components/Product.vue"

import axios from 'axios'

import {Toast} from 'vant'
import { useRouter } from "vue-router";

export default defineComponent({
  name: 'Like',
  components: {
    Navi,
    Bg,
    Product
  },
  setup () {

    let router: any = useRouter();

    let productData: any = ref<Array<any>>([]);

    //获取用户所有收藏商品
    let getLikeData: () => void = (): void => {
         axios({
          method: 'get',
          url: '/findAllLike'
        }).then((result: any): void => {
          

          if (result.data.code === 700) {
            Toast(result.data.msg);
            router.push({name: 'Login'});
            return;
          }

          if (result.data.code === 2000) {
            productData.value = result.data.result;
          }

         
        }).catch((err: any): void => {
          
          
        })
      }

    //查看商品详情
    let viewProductDetail: (pid: string) => void = (pid: string): void => {
        router.push({name: 'Detail', params: {pid}})
      }

    //删除收藏商品
    let removeProduct: (pid: string, index: number) => void = (pid: string, index: number): void => {
        
        axios({
          method: 'post',
          url: '/notlike',
          data: {
            pid
          }
        }).then((result: any): void => {
          

          if (result.data.code === 700) {
            Toast(result.data.msg);
            router.push({name: 'Login'});
            return;
          }

          if (result.data.code === 900 && result.data.result === 1) {

            Toast('删除成功');
            productData.value.splice(index, 1);
          } else {
            Toast('删除失败');
          }

         
        }).catch((err: any): void => {
          
          
        })
      }

    onMounted(() => {
      getLikeData();
    })

    return {
      productData,
      viewProductDetail,
      removeProduct
    };
  }
})
